import React, { useState } from 'react';
import Product from './components/comps/product';

import { TonConnectUIProvider } from '@tonconnect/ui-react';
import css from "./styles/product.css";
import { Header } from './components/comps/header';
import { Address } from './components/comps/address';
import { Settings } from './components/comps/settings';

const { ProductContainer } = css;

function App() {
  const [cart, setCart] = useState({"Tier 1": 0, "Tier 2": 0, "Tier 3": 0});

  const showCart = () => {
    if (cart["Tier 1"] + cart["Tier 2"] + cart["Tier 3"] === 0) {
      return <></>
    } else {
      const total = Math.round((cart["Tier 1"] * 0.03 + cart["Tier 2"] * 0.02 + cart["Tier 3"] * 0.01) * 100) / 100;
      return <div>
        <h1>Cart</h1>
        <h3>Total: {total} TON</h3>
        <Settings total={total}/>
        <br/>
        <button onClick={() => setCart({"Tier 1": 0, "Tier 2": 0, "Tier 3": 0})}>Clear Cart</button>
      </div>
    }

  }

  return (
    <React.Fragment>
      {/*<TonConnectUIProvider manifestUrl="https://raw.githubusercontent.com/Dayforce/ton-connector/gh-pages/ton-manifest1.json">*/}
      <Header/>
      <Address/>
      <ProductContainer>
        <Product productName={"Tier 1"} productPrice={0.03} quantity={cart["Tier 1"]} setCart={setCart}/>
        <Product productName={"Tier 2"} productPrice={0.02} quantity={cart["Tier 2"]} setCart={setCart}/>
        <Product productName={"Tier 3"} productPrice={0.01} quantity={cart["Tier 3"]} setCart={setCart}/>
      </ProductContainer>
      {showCart()}
      {/*</TonConnectUIProvider>*/}
    </React.Fragment>
  );
}

export default App;
