import { useTonConnectUI } from '@tonconnect/ui-react';

export const Settings = (props) => {
    const [tonConnectUI, setOptions] = useTonConnectUI();
    function sendTransaction() {
        const transaction = {
            messages: [
                {
                    address: "EQDJrK7jPlH3vmPoBMXsD0MLI3ZDxfBPb0IasM7Dkk5LxLAI",
                    amount: (props.total * 1000000000).toString(),
                }
            ]
        }
        const newTransaction = tonConnectUI.sendTransaction(transaction, {
            modals: ["before", "error", "success"],
            notifications: ["before", "success", "error"],
            
        });
        newTransaction.then((result) => {
            const boc = result.boc
            console.log("boc: ", boc)
        })
    }
    return (
        <div>
            <button onClick={sendTransaction}>
                Buy
            </button>
        </div>
    );
};